
import { supabase } from '../lib/supabase';
import { v4 as uuidv4 } from 'uuid';

export interface UploadResult {
    url: string;
    path: string;
}

/**
 * Upload Service
 *  Supabase Storage
 */
export const uploadService = {
    /**
     * 
     * @param file 
     * @param bucket Bucket  ( 'community-images')
     * @param folder  ()
     */
    async uploadImage(file: File, bucket = 'community-images', folder = 'posts'): Promise<UploadResult> {
        // 1. 
        if (!file.type.startsWith('image/')) {
            throw new Error('');
        }
        const maxSize = 5 * 1024 * 1024; // 5MB
        if (file.size > maxSize) {
            throw new Error(' 5MB');
        }

        // 2. 
        const ext = file.name.split('.').pop();
        const fileName = `${uuidv4()}.${ext}`;
        const filePath = folder ? `${folder}/${fileName}` : fileName;

        // 3. 
        const { data, error } = await supabase.storage
            .from(bucket)
            .upload(filePath, file, {
                cacheControl: '3600',
                upsert: false
            });

        if (error) {
            console.error('[UploadService] Upload failed:', error);
            throw error;
        }

        // 4. 
        const { data: { publicUrl } } = supabase.storage
            .from(bucket)
            .getPublicUrl(filePath);

        return {
            url: publicUrl,
            path: filePath
        };
    },

    /**
     * D2:  (Batch Upload)
     */
    async uploadFiles(files: File[], bucket = 'community-images', folder = 'posts'): Promise<UploadResult[]> {
        return Promise.all(files.map(file => this.uploadImage(file, bucket, folder)));
    }
};
