import React, { useState, useCallback } from 'react';
import { Check, Plus, X } from 'lucide-react';

// 
const HIGHLIGHT_CATEGORIES = [
  {
    name: '',
    tags: ['', '', ''],
  },
  {
    name: '',
    tags: ['', '', '', '', ''],
  },
  {
    name: '',
    tags: ['', '', '', ''],
  },
  {
    name: '',
    tags: ['', '', '', ''],
  },
  {
    name: '',
    tags: ['', '', ''],
  },
];

const MAX_HIGHLIGHTS = 5;
const MIN_HIGHLIGHTS = 3;
const MAX_CUSTOM_TAGS = 3;
const MAX_TAG_LENGTH = 5;

interface HighlightPickerProps {
  value: string[];
  onChange: (highlights: string[]) => void;
  required?: boolean;
}

export const HighlightPicker: React.FC<HighlightPickerProps> = ({
  value,
  onChange,
  required = false,
}) => {
  const [customInputs, setCustomInputs] = useState<string[]>(['', '', '']);

  // 
  const toggleTag = useCallback((tag: string) => {
    if (value.includes(tag)) {
      // 
      onChange(value.filter((t) => t !== tag));
    } else if (value.length < MAX_HIGHLIGHTS) {
      // 
      onChange([...value, tag]);
    }
  }, [value, onChange]);

  // 
  const handleCustomInput = useCallback((index: number, input: string) => {
    // 
    const trimmed = input.slice(0, MAX_TAG_LENGTH);
    const newInputs = [...customInputs];
    newInputs[index] = trimmed;
    setCustomInputs(newInputs);
  }, [customInputs]);

  // 
  const addCustomTag = useCallback((index: number) => {
    const tag = (customInputs[index] || '').trim();
    if (!tag) return;
    if (value.includes(tag)) return; // 
    if (value.length >= MAX_HIGHLIGHTS) return; // 

    onChange([...value, tag]);
    
    // 
    const newInputs = [...customInputs];
    newInputs[index] = '';
    setCustomInputs(newInputs);
  }, [customInputs, value, onChange]);

  // 
  const removeTag = useCallback((tag: string) => {
    onChange(value.filter((t) => t !== tag));
  }, [value, onChange]);

  // 
  const selectedCount = value.length;
  const isValid = selectedCount >= MIN_HIGHLIGHTS;
  const isFull = selectedCount >= MAX_HIGHLIGHTS;

  return (
    <div className="space-y-4">
      {/*  */}
      <div className="flex items-center justify-between">
        <span className="text-xs font-medium text-slate-600">
           {required && '*'} <span className="text-slate-400"> 3-5  5 </span>
        </span>
        <span className={`text-xs font-medium ${isValid ? 'text-green-600' : 'text-slate-400'}`}>
           {selectedCount}/{MAX_HIGHLIGHTS} {isValid && ''}
        </span>
      </div>

      {/*  */}
      <div className="space-y-3 rounded-xl border border-slate-200 bg-slate-50 p-4">
        {HIGHLIGHT_CATEGORIES.map((category) => (
          <div key={category.name}>
            <span className="mb-1.5 block text-xs font-medium text-slate-500">{category.name}</span>
            <div className="flex flex-wrap gap-2">
              {category.tags.map((tag) => {
                const isSelected = value.includes(tag);
                const isDisabled = !isSelected && isFull;
                
                return (
                  <button
                    key={tag}
                    type="button"
                    onClick={() => toggleTag(tag)}
                    disabled={isDisabled}
                    className={`flex items-center gap-1 rounded-full px-3 py-1.5 text-sm font-medium transition-all ${
                      isSelected
                        ? 'bg-[#003366] text-white shadow-sm'
                        : isDisabled
                        ? 'cursor-not-allowed bg-slate-200 text-slate-400'
                        : 'bg-white text-slate-600 shadow-sm hover:bg-blue-50 hover:text-[#003366]'
                    }`}
                  >
                    {isSelected && <Check size={14} />}
                    {tag}
                  </button>
                );
              })}
            </div>
          </div>
        ))}

        {/*  */}
        <div>
          <span className="mb-1.5 block text-xs font-medium text-slate-500">
             {MAX_CUSTOM_TAGS} 
          </span>
          <div className="flex flex-wrap gap-2">
            {customInputs.map((input, index) => (
              <div key={index} className="flex items-center gap-1">
                <input
                  type="text"
                  value={input}
                  onChange={(e) => handleCustomInput(index, e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      e.preventDefault();
                      addCustomTag(index);
                    }
                  }}
                  placeholder="..."
                  maxLength={MAX_TAG_LENGTH}
                  disabled={isFull}
                  className="w-20 rounded-lg border border-slate-200 bg-white px-2 py-1.5 text-sm text-slate-700 placeholder:text-slate-300 focus:border-[#003366] focus:outline-none focus:ring-1 focus:ring-[#003366] disabled:cursor-not-allowed disabled:bg-slate-100"
                />
                {input.trim() && !isFull && (
                  <button
                    type="button"
                    onClick={() => addCustomTag(index)}
                    className="rounded-full bg-[#003366] p-1 text-white transition hover:bg-[#004488]"
                  >
                    <Plus size={14} />
                  </button>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>

      {/*  */}
      {value.length > 0 && (
        <div className="flex flex-wrap items-center gap-2">
          <span className="text-xs text-slate-500"></span>
          {value.map((tag) => (
            <span
              key={tag}
              className="flex items-center gap-1 rounded-full bg-blue-100 px-2.5 py-1 text-sm font-medium text-[#003366]"
            >
              {tag}
              <button
                type="button"
                onClick={() => removeTag(tag)}
                className="rounded-full p-0.5 text-blue-400 transition hover:bg-blue-200 hover:text-blue-600"
              >
                <X size={12} />
              </button>
            </span>
          ))}
        </div>
      )}

      {/*  */}
      {required && !isValid && value.length > 0 && (
        <p className="text-xs text-amber-600">
           {MIN_HIGHLIGHTS} 
        </p>
      )}
    </div>
  );
};
