export async function getFeaturedProperties(): Promise<FeaturedProperty[]> {
  try {
    //  Timeout  ()
    const response = await fetch('/api/home/featured-properties');
    
    if (!response.ok) {
      console.warn('[propertyService] API  200:', response.status);
      return [];
    }
    
    const json = await response.json();
    
    if (json.success && Array.isArray(json.data)) {
      return json.data;
    }
    
    console.warn('[propertyService] API :', json);
    return [];
  } catch (error) {
    console.error('[propertyService] getFeaturedProperties :', error);
    return []; // Level 3:  Mock
  }
}
