import { useCallback } from 'react'
import { useNavigate } from 'react-router-dom'
import { useQuery } from '@tanstack/react-query'
import { BACKUP_REVIEWS } from '../../../constants/data'
import { HomeCard } from '../components/HomeCard'
import { ReviewCard } from '../components/ReviewCard'
import { getFeaturedHomeReviews } from '../../../services/communityService'
import type { ReviewForUI } from '../../../types/review'

// V4: Extract hardcoded URL
const SEED_REVIEWS_URL = '/maihouses/community-wall_mvp.html';

/**
 *  ReviewForUI  ReviewCard 
 * - displayId: ReviewForUI.displayId ( ReviewCard)
 * - originalId: ReviewForUI.id ( UUID React key)
 * -  source  communityId 
 */
interface ReviewWithNavigation {
  originalId: string;   //  UUID React key
  displayId: string;    //  ( ReviewCard)
  name: string;
  rating: number;
  tags: string[];
  content: string;
  source: 'real' | 'seed';
  communityId: string | null;
}

/**
 *  API  ReviewForUI 
 *  UUID  React keydisplayId 
 */
function mapToReviewWithNavigation(review: ReviewForUI): ReviewWithNavigation {
  return {
    originalId: review.id,      //  UUID
    displayId: review.displayId, // 
    name: review.name,
    rating: review.rating,
    tags: review.tags,
    content: review.content,
    source: review.source,
    communityId: review.communityId,
  };
}

/**
 *  BACKUP_REVIEWS 
 * backup.id  originalId  displayId
 * V3: BACKUP_REVIEWS  source  communityId
 */
function mapBackupToReviewWithNavigation(backup: typeof BACKUP_REVIEWS[number]): ReviewWithNavigation {
  return {
    originalId: backup.id, // H5: Use stable ID directly
    displayId: backup.id,  // 
    name: backup.name,
    rating: backup.rating,
    tags: backup.tags,
    content: backup.content,
    source: backup.source,
    communityId: backup.communityId,
  };
}

/**
 * P9-3: 
 * 
 *  React Query  API  (V2)
 * - Loading  skeleton
 * - Error  BACKUP_REVIEWS 
 * -  real seed 
 */
export default function CommunityTeaser() {
  const navigate = useNavigate();

  // V2: Use React Query instead of useEffect + useState
  const { data: apiReviews, isLoading, isError } = useQuery({
    queryKey: ['featured-reviews'],
    queryFn: getFeaturedHomeReviews,
    staleTime: 5 * 60 * 1000, // 5 minutes
    retry: 1, // U2: Retry once (handled by React Query now)
  });

  // Determine which reviews to show
  // If error or no data, fallback to BACKUP_REVIEWS
  const reviews = isError || !apiReviews
    ? BACKUP_REVIEWS.map(mapBackupToReviewWithNavigation)
    : apiReviews.map(mapToReviewWithNavigation);

  // V1: Extract click handler with useCallback to avoid duplication
  const handleReviewClick = useCallback((review: ReviewWithNavigation) => {
    if (review.source === 'real' && review.communityId) {
      navigate(`/community/${review.communityId}/wall`);
    } else {
      window.location.href = SEED_REVIEWS_URL; // V4: Use constant
    }
  }, [navigate]);

  // Loading skeleton
  if (isLoading) {
    return (
      <HomeCard className="bg-white/96 border border-[#E6EDF7] p-3 shadow-[0_4px_20px_rgba(0,56,90,0.03)] backdrop-blur-md">
        <div className="relative mb-4 flex items-center justify-between overflow-hidden rounded-2xl bg-gradient-to-r from-[#00385a] to-[#005585] px-4 py-3 shadow-[0_2px_8px_rgba(0,56,90,0.15)]">
          <div className="pointer-events-none absolute -right-6 -top-6 size-24 rounded-full bg-white/5 blur-2xl" />
          <div className="relative z-10 flex items-center gap-2.5">
            <div className="size-1.5 rounded-full bg-[#E63946] shadow-[0_0_8px_rgba(230,57,70,0.6)]" />
            <h3 className="text-shadow-sm m-0 text-lg font-black tracking-wide text-white"></h3>
            <span className="rounded-full border border-white/20 bg-white/95 px-2.5 py-0.5 text-[11px] font-bold text-[#00385a] shadow-sm backdrop-blur-sm">
              
            </span>
          </div>
        </div>

        {/* Skeleton Grid */}
        <div className="grid grid-cols-1 gap-3 sm:grid-cols-2">
          {[1, 2, 3, 4, 5, 6].map((i) => (
            <div key={i} className="animate-pulse rounded-2xl border border-[#E6EDF7] bg-white p-3.5">
              <div className="flex gap-3">
                <div className="size-[38px] rounded-full bg-gray-200" />
                <div className="flex-1 space-y-2">
                  <div className="h-4 w-3/4 rounded bg-gray-200" />
                  <div className="h-3 w-1/2 rounded bg-gray-200" />
                  <div className="h-3 w-full rounded bg-gray-200" />
                  <div className="h-3 w-5/6 rounded bg-gray-200" />
                </div>
              </div>
            </div>
          ))}
        </div>

        <div className="mt-4 h-12 animate-pulse rounded-xl bg-gray-100" />
      </HomeCard>
    );
  }

  return (
    <HomeCard className="bg-white/96 border border-[#E6EDF7] p-3 shadow-[0_4px_20px_rgba(0,56,90,0.03)] backdrop-blur-md">
      <div className="relative mb-4 flex items-center justify-between overflow-hidden rounded-2xl bg-gradient-to-r from-[#00385a] to-[#005585] px-4 py-3 shadow-[0_2px_8px_rgba(0,56,90,0.15)]">
        {/* Decorative circle for texture */}
        <div className="pointer-events-none absolute -right-6 -top-6 size-24 rounded-full bg-white/5 blur-2xl" />

        <div className="relative z-10 flex items-center gap-2.5">
          <div className="size-1.5 rounded-full bg-[#E63946] shadow-[0_0_8px_rgba(230,57,70,0.6)]" />
          <h3 className="text-shadow-sm m-0 text-lg font-black tracking-wide text-white"></h3>
          <span className="rounded-full border border-white/20 bg-white/95 px-2.5 py-0.5 text-[11px] font-bold text-[#00385a] shadow-sm backdrop-blur-sm">
            
          </span>
        </div>
        
        {/* Error indicator -  UI */}
        {isError && (
          <span className="relative z-10 rounded-full bg-yellow-500/20 px-2 py-0.5 text-[10px] text-yellow-100">
            
          </span>
        )}
      </div>

      <div className="grid grid-cols-1 gap-3 sm:grid-cols-2">
        {reviews.map((review) => (
          <div
            key={review.originalId}
            onClick={() => handleReviewClick(review)}
            onKeyDown={(e) => {
              if (e.key === 'Enter' || e.key === ' ') { // Fix Lie 7: Support Space key
                e.preventDefault(); // Prevent scrolling for Space
                handleReviewClick(review);
              }
            }}
            role="button"
            tabIndex={0}
            className="cursor-pointer"
            aria-label={` ${review.name} `}
          >
            <ReviewCard
              id={review.displayId}
              name={review.name}
              rating={review.rating}
              tags={review.tags}
              content={review.content}
            />
          </div>
        ))}
      </div>

      <a
        className="group relative mt-4 flex items-center gap-2.5 rounded-xl border border-[#E6EDF7] bg-gradient-to-b from-white to-[#F6F9FF] p-3.5 font-black text-[#00385a] no-underline transition-all duration-200 hover:border-[#00385a]/20 hover:shadow-[0_4px_12px_rgba(0,56,90,0.08)] active:translate-y-px lg:justify-center lg:text-center"
        href={SEED_REVIEWS_URL}
        aria-label=""
      >
        <span className="text-[15px] tracking-wide transition-colors group-hover:text-[#004E7C] lg:mx-auto">
          
        </span>
        <span className="ml-auto rounded-lg bg-[#00385a] px-3 py-1.5 text-xs font-bold text-white shadow-sm transition-colors group-hover:bg-[#004E7C] lg:absolute lg:right-[14px] lg:top-1/2 lg:ml-0 lg:-translate-y-1/2">
           
        </span>
      </a>
    </HomeCard>
  )
}

