/**
 * 
 * 
 * 
 * 
 */

// 
const SENSITIVE_WORDS = [
  // 
  '', '', '', '', '', '',
  // /
  '', '', '', '',
  // 
  '', '', '',
];

// 
const AD_WORDS = [
  'LINE', '', '', '', '',
  '', 'bit.ly', 'tinyurl', '',
  '', '', '',
];

// 
const COMMUNITY_NAME_BLACKLIST = [
  '', '', '', '', '', '',
  '', '', '', '', '', '',
  '', '', '', '',
  'A', 'B', 'C', 'A', 'B', 'C',
];

/**
 *  Regex  (Google )
 */
const escapeRegex = (str: string) => str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');

// 1.  Regex ()
const SENSITIVE_REGEX = new RegExp(
  SENSITIVE_WORDS.map(w => escapeRegex(w.replace(/[^\u4e00-\u9fa5a-zA-Z0-9]/g, '').toLowerCase())).join('|'),
  'i'
);

// 2.  Regex ()
const AD_REGEX_PATTERN = AD_WORDS.map(word => 
  word.split('').map(char => escapeRegex(char)).join('[^\\u4e00-\\u9fa5a-zA-Z0-9]{0,2}')
).join('|');
const AD_REGEX = new RegExp(AD_REGEX_PATTERN, 'i');

// 3.  Regex
const COMMUNITY_BLACKLIST_REGEX = new RegExp(`^(${COMMUNITY_NAME_BLACKLIST.map(escapeRegex).join('|')})$`, 'i');

export interface ContentCheckResult {
  passed: boolean;
  issues: {
    type: 'sensitive' | 'ad' | 'blacklist' | 'format';
    word?: string;
    message: string;
  }[];
}

/**
 * 
 *  Regex ..-L-I-N-E
 *  Regex  O(N) 
 */
export function checkContent(text: string): ContentCheckResult {
  const issues: ContentCheckResult['issues'] = [];
  if (!text) return { passed: true, issues: [] };
  
  //  ()
  const cleanText = text.replace(/[^\u4e00-\u9fa5a-zA-Z0-9]/g, '').toLowerCase();

  // 1.  ( Regex)
  const sensitiveMatch = cleanText.match(SENSITIVE_REGEX);
  if (sensitiveMatch) {
    issues.push({
      type: 'sensitive',
      word: sensitiveMatch[0],
      message: ``,
    });
  }

  // 2.  ( Regex )
  const adMatch = text.match(AD_REGEX);
  if (adMatch) {
    issues.push({
      type: 'ad',
      word: adMatch[0],
      message: ``,
    });
  }

  return {
    passed: issues.length === 0,
    issues,
  };
}

/**
 * 
 */
export function checkCommunityName(name: string): ContentCheckResult {
  const issues: ContentCheckResult['issues'] = [];
  const trimmed = name.trim();

  // 
  if (trimmed.length < 2) {
    issues.push({
      type: 'format',
      message: ' 2 ',
    });
  }

  if (trimmed.length > 30) {
    issues.push({
      type: 'format',
      message: ' 30 ',
    });
  }

  //  ( Regex)
  if (COMMUNITY_BLACKLIST_REGEX.test(trimmed)) {
    issues.push({
      type: 'blacklist',
      message: `${trimmed}`,
    });
  }

  // +
  if (/^.*[]\d+?$/.test(trimmed) && !/|||/.test(trimmed)) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  if (/^\d+$/.test(trimmed)) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  if (/^[a-zA-Z\s]+$/.test(trimmed) && trimmed.length < 5) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  for (const word of ['', '', '', '', '', '']) {
    if (trimmed.includes(word)) {
      issues.push({
        type: 'ad',
        word,
        message: `${word}`,
      });
    }
  }

  return {
    passed: issues.length === 0,
    issues,
  };
}

/**
 * 
 */
export function checkPropertyReview(text: string, type: 'advantage' | 'disadvantage'): ContentCheckResult {
  const issues: ContentCheckResult['issues'] = [];
  const trimmed = text.trim();

  // 
  const contentCheck = checkContent(trimmed);
  issues.push(...contentCheck.issues);

  // /
  if (trimmed.length > 0 && trimmed.length < 3) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  if (/^[""'']+$/.test(trimmed)) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  // 
  if (/(.)\1{4,}/.test(trimmed)) {
    issues.push({
      type: 'format',
      message: '',
    });
  }

  return {
    passed: issues.length === 0,
    issues,
  };
}

export default {
  checkContent,
  checkCommunityName,
  checkPropertyReview,
};
