/**
 * usePermission Hook (L7+ Optimized)
 * 
 *  Hook
 *  useAuth  ROLE_PERMISSIONS 
 * 
 * :
 * 1. O(1) :  Set  Array.includes
 * 2. :  hasAny / hasAll
 * 3. :  Loading  Unauthenticated
 */

import { useCallback, useMemo } from 'react';
import { useAuth } from '../hooks/useAuth';
import { Permission, ROLE_PERMISSIONS } from '../types/permissions';
import { Role } from '../types/community';

// P7-Audit-C2: Export return type for strict mocking
export interface UsePermissionReturn {
    hasPermission: (permission: Permission) => boolean;
    hasAnyPermission: (requiredPermissions: Permission[]) => boolean;
    hasAllPermissions: (requiredPermissions: Permission[]) => boolean;
    role: Role | undefined;
    isAuthenticated: boolean;
    isLoading: boolean;
    permissions: Set<Permission>;
}
export function usePermission(): UsePermissionReturn {
    const { user, role, isAuthenticated, loading: authLoading } = useAuth();

    /**
     *  (O(1) Lookup)
     *  Set Memoize 
     */
    const permissions = useMemo<Set<Permission>>(() => {
        if (!isAuthenticated || !role) {
            return new Set();
        }

        // P7-Audit-C1: Strict Type Guard
        const isValidRole = (r: string | undefined): r is Role => {
            return typeof r === 'string' && Object.keys(ROLE_PERMISSIONS).includes(r);
        };

        if (isValidRole(role)) {
            return new Set(ROLE_PERMISSIONS[role]);
        }

        return new Set();
    }, [isAuthenticated, role]);

    /**
     * 
     * @param permission - 
     */
    const hasPermission = useCallback((permission: Permission): boolean => {
        return permissions.has(permission);
    }, [permissions]);

    /**
     *  (OR)
     * @param requiredPermissions - 
     */
    const hasAnyPermission = useCallback((requiredPermissions: Permission[]): boolean => {
        return requiredPermissions.some(p => permissions.has(p));
    }, [permissions]);

    /**
     *  (AND)
     * @param requiredPermissions - 
     */
    const hasAllPermissions = useCallback((requiredPermissions: Permission[]): boolean => {
        return requiredPermissions.every(p => permissions.has(p));
    }, [permissions]);

    return {
        hasPermission,
        hasAnyPermission,
        hasAllPermissions,
        role,
        isAuthenticated,
        isLoading: authLoading, // Forward auth loading state
        permissions, // Expose raw Set for advanced usage (debugging)
    };
}
